.register-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
}

.register-left {
  width: 65%;
  height: 100%;
  background: url("./BG.jpg");
  background-size: cover;
  background-position: center;
}

.register-right {
  width: 35%;
  height: 100%;
  padding: 0px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: white;
  position: relative;
  min-width: 300px;
}

.register-right-top {
  margin-bottom: 40px;
}

.register-right-top h2 {
  margin-bottom: 5px;
  color: rgb(48, 48, 48);
}

.register-right-top p {
  color: rgb(105, 105, 105);
  font-size: 15px;
}

.register-right-btm {
  display: flex;
  flex-direction: column;
}

.register-right-btm input {
  margin-bottom: 20px;
  border-style: none;
  outline-style: none;
  border: 1px solid lightgrey;
  width: 100%;
  height: 50px;
  border-radius: 10px;
  padding-left: 15px;
  background: rgb(255, 255, 255);
}

.register-right-btm input:focus {
  border: 1px solid grey;
}

.contact-input {
  width: 100%;
  height: 50px;
  position: relative;
  display: flex;
  margin-bottom: 20px;
}

.contact-input input {
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
  border-style: none;
  outline-style: none;
  border: 1px solid lightgrey;
  width: 100%;
  height: 50px;
  border-radius: 10px;
  padding-left: 60px;
  background: rgb(255, 255, 255);
}

.contact-input p {
  width: 50px;
  height: 50px;
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: grey;
}

.contact-input p::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 50%;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  border-right: 1px solid lightgrey;
}

.sign-up-wrapper {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.sign-up-wrapper .register-btn {
  width: 120px;
  height: 100%;
  cursor: pointer;
  border-style: none;
  outline-style: none;
  background: rgb(14, 96, 247);
  color: white;
  border-radius: 5px;
  transition: 0.2s ease-out;
  font-weight: 500;
  font-size: 15px;
  margin-bottom: 0;
  padding: 0;
}

.register-btn:hover {
  transition: 0.2s ease-in;
  background: rgb(12, 75, 194);
}
