.card-item {
  min-width: 210px;
  height: 300px;
  background: rgb(255, 255, 255);
  border-radius: 5px;
  margin: 30px 10px 0px 10px;
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

.card-item:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  transform: scale(1.01);
  transition: 0.1s ease-in-out;
  border: 1px solid lightgray;
}

.card-item:hover .card-add-to-cart {
  opacity: 1;
}

.card-item:hover .card-add-to-cart-diactive {
  opacity: 0.5;
}

.card-item:hover .card-top img {
  filter: blur(4px);
  transition: 0.1s ease-in-out;
}

.card-top {
  width: 100%;
  height: 75%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-radius: 2px;
  border: 1px solid lightgrey;
}

.card-top img {
  object-fit: cover;
  transition: 0.2s ease-out;
  width: 115%;
  position: absolute;
}

.card-btm {
  width: 100%;
  height: 25%;
  padding-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.card-btm p {
  font-size: 14px;
  color: #3a3a3a;
}

.card-add-to-cart {
  background: rgb(14, 96, 247);
  color: white;
  padding: 5px 10px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.2s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}

.card-add-to-cart-diactive {
  background: rgb(14, 96, 247);
  color: white;
  padding: 5px 10px;
  font-size: 14px;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.2s ease-in;
  pointer-events: none;
  position: absolute;
  max-width: 500px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}

.card-add-to-cart:hover {
  transition: 0.2s ease-in;
  background: rgb(12, 75, 194);
}
