.reservation-form {
  width: 90%;
  height: 90vh;
  background: rgb(255, 255, 255);
  position: fixed;
  top: 50%;
  left: 50%;
  border: 1px solid lightgrey;
  border-radius: 10px;
  transform: translate(-50%, -50%);
  z-index: 5;
  box-shadow: 0px 8px 30px 0px rgba(49, 68, 80, 0.2);
  display: flex;
}

.reservation-form i {
  position: absolute;
  font-size: 20px;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgb(34, 34, 34);
  transition: 0.2s ease-out;
}

.reservation-form i:hover {
  transition: 0.2s ease-in;
  background: rgb(226, 226, 226);
}

.reserve-left {
  width: 60%;
  height: 100%;
}

.reserve-right {
  width: 40%;
  height: 100%;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(241, 241, 241);
}

.reservation-header {
  width: 80%;
  height: 10%;
  min-height: 50px;
  display: flex;
  align-items: center;
  color: rgb(34, 34, 34);
  margin: 0 auto;
}

.reservation-body {
  width: 100%;
  height: 90%;
  overflow-y: scroll;
  padding: 50px 0px;
}

.reserve-table {
  width: 80%;
  height: auto;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.reserve-table-item {
  width: 48.4%;
  height: 100px;
  margin-bottom: 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.1s ease-out;
  border: 1px solid rgba(211, 211, 211, 0.253);
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: rgb(34, 34, 34);
}

.reserve-table-item-active {
  width: 48.4%;
  height: 100px;
  margin-bottom: 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.1s ease-out;
  border: 1px solid rgba(211, 211, 211, 0.253);
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: rgb(255, 255, 255);
  box-shadow: 0px 8px 30px 0px rgba(93, 112, 124, 0.1);
  background: rgba(65, 202, 95, 0.7);
}

.reserve-table-item:hover {
  transition: 0.1s ease-in;
  border: 1px solid lightgrey;
  box-shadow: 0px 8px 30px 0px rgba(93, 112, 124, 0.1);
}

.inactive {
  width: 12px;
  height: 12px;
  background: rgba(211, 211, 211, 0.5);
  position: absolute;
  top: 10px;
  left: 10px;
  border-radius: 100px;
}

.active {
  width: 12px;
  height: 12px;
  background: rgba(255, 255, 255);
  position: absolute;
  top: 10px;
  left: 10px;
  border-radius: 100px;
}
