.header {
  width: 100%;
  height: 70px;
  position: absolute;
  display: flex;
  justify-content: center;
  z-index: 4;
  top: 0;
}

.header-active {
  width: 100%;
  height: 70px;
  position: fixed;
  display: flex;
  justify-content: center;
  z-index: 4;
  top: 0;
  background: #fff;
  box-shadow: rgba(100, 100, 111, 0.1) 0px 7px 29px 0px;
  border-bottom: 1px solid rgb(226, 226, 226);
}

.navbar {
  width: 75%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

.brand-name {
  text-decoration: none;
}

.navbar h2 {
  font-size: 20px;
  cursor: pointer;
  transition: 0.1s ease-out;
  color: rgb(48, 48, 48);
  text-decoration: none;
}

.navbar h2:hover {
  color: #e28a46;
  transition: 0.2s ease-out;
}

.nav-links {
  display: flex;
  list-style: none;
}

.active-link {
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  color: #e28a46;
  display: flex;
  align-items: center;
  padding: 8px 10px;
  margin: 0px 5px;
  position: relative;
}

.active-link::after {
  content: "";
  width: 50%;
  height: 2px;
  background: #e28a46;
  position: absolute;
  border-radius: 100px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.active-link i {
  margin-left: 5px;
  position: relative;
}

.link {
  text-decoration: none;
  color: rgb(48, 48, 48);
  transition: 0.1s ease-in;
  font-size: 14px;
  font-weight: 500;
  padding: 8px 10px;
  border-radius: 5px;
  margin: 0px 5px;
}
.link:hover {
  color: #e28a46;
}

.login-link {
  color: #e28a46;
  border: 1px solid #e28a46;
  border-radius: 0;
}

.login-link:hover {
  background: #e28a46;
  color: #fff;
}

.special-link i {
  margin-left: 5px;
  position: relative;
}

.special-link {
  display: flex;
  align-items: center;
  margin: 0px 5px;
}

.shopping-cart-badge {
  background: rgb(233, 53, 53);
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  border-radius: 100px;
  margin-right: 8px;
  font-size: 12px;
  right: -20px;
  position: absolute;
  top: -15px;
}

.profile-nav {
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-nav-drop {
  width: 38px;
  height: 38px;
  border-radius: 100px;
  margin-left: 10px;
  transition: 0.2s ease-in;
  background: #f0f2f7;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  cursor: pointer;
  font-size: 22px;
  color: rgb(48, 48, 48);
  position: relative;
  border: 4px solid #f0f2f700;
}

.profile-nav-drop-active {
  width: 38px;
  height: 38px;
  border-radius: 100px;
  margin-left: 10px;
  transition: 0.2s ease-in;
  background: #e2e3e7;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  cursor: pointer;
  font-size: 22px;
  color: rgb(48, 48, 48);
  position: relative;
  border: 4px solid #f0f2f7;
}

.profile-nav-drop i {
  color: rgb(48, 48, 48);
}

.profile-nav-drop-active i {
  color: rgb(55, 123, 248);
}

.profile-nav-drop:hover {
  transition: 0.2s ease-in;
  background: #e2e3e7;
}

.nav-username {
  font-size: 14px;
  font-weight: 500;
}

.profile-nav-drop-after {
  width: 230px;
  height: 160px;
  background: rgb(255, 255, 255);
  position: absolute;
  top: 120%;
  right: 0;
  z-index: 3;
  border-radius: 5px;
  border: 1px solid rgb(226, 226, 226);
  box-shadow: 0px 8px 30px 0px rgba(49, 68, 80, 0.1);

  padding: 5px;
}

.profile-nav-drop-after-top {
  height: 45%;
  width: 100%;
  padding-bottom: 5px;
  position: relative;
}

.profile-nav-drop-after-top-wrapper {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  transition: 0.2s ease-out;
  z-index: 4;
  display: flex;
  align-items: center;
  padding: 0px 10px;
}

.left {
  width: 40px;
  height: 40px;
  background: #e2e3e7;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.left i {
  color: rgb(48, 48, 48);
  font-size: 16px;
}

.right {
  height: 100%;
  width: 80%;
  padding-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.right h5 {
  font-weight: 500;
  font-size: 14px;
}

.right p {
  font-weight: 300;
  font-size: 12px;
}

.profile-nav-drop-after-top-wrapper:hover {
  border-radius: 5px;
  transition: 0.2s ease-in;
  background: #f0f2f7;
}

.profile-nav-drop-after-top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 80%;
  height: 100%;
  border-bottom: 1px solid lightgrey;
  transform: translateX(-50%);
  z-index: -1;
}

.profile-nav-drop-after-btm {
  width: 100%;
  height: 55%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding-top: 5px;
}

.profile-nav-drop-after-btm1,
.profile-nav-drop-after-btm2 {
  width: 100%;
  height: 50%;
  border-radius: 5px;
  font-size: 14px;
  display: flex;
  align-items: center;
  padding-left: 10px;
  transition: 0.2s ease-out;
  font-weight: 500;
}

.profile-nav-drop-after-btm1:hover,
.profile-nav-drop-after-btm2:hover {
  transition: 0.2s ease-in;
  background: #f0f2f7;
}
