.login-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
}

.login-left {
  width: 65%;
  height: 100%;
  background: url("./BG.jpg");
  background-size: cover;
  background-position: center;
}

.login-right {
  width: 35%;
  height: 100%;
  padding: 0px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: white;
  position: relative;
}
.login-right-top {
  margin-bottom: 40px;
}

.login-right-top h2 {
  margin-bottom: 5px;
  color: rgb(48, 48, 48);
}

.login-right-top p {
  color: rgb(105, 105, 105);
  font-size: 15px;
}

.login-right form {
  display: flex;
  flex-direction: column;
}

.login-right-btm input:focus {
  border: 1px solid rgba(40, 115, 255, 0.74);
}

.username,
.password {
  width: 100%;
  height: 50px;
  position: relative;
  display: flex;
  margin-bottom: 20px;
}

.username i,
.password i {
  position: absolute;
  width: 50px;
  height: 100%;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: grey;
  font-size: 14px;
  cursor: pointer;
  transition: 0.1s ease-out;
}

.username i:hover,
.password i:hover {
  color: rgb(14, 96, 247);
  transition: 0.1s ease-in;
}

.username i::after,
.password i::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 50%;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  border-right: 1px solid lightgrey;
}

.password div {
  width: 50px;
  height: 46px;
  background: rgb(255, 255, 255);
  position: absolute;
  right: 1px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.password div i::after {
  display: none;
}

.username input,
.password input {
  margin-bottom: 20px;
  border-style: none;
  outline-style: none;
  border: 1px solid lightgrey;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  padding-left: 60px;
  background: rgb(255, 255, 255);
}

.sign-wrapper {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.login-btn {
  width: 120px;
  height: 100%;
  cursor: pointer;
  border-style: none;
  outline-style: none;
  background: rgb(14, 96, 247);
  color: white;
  border-radius: 5px;
  transition: 0.2s ease-out;
  font-weight: 500;
  font-size: 15px;
}

.login-btn-diactive {
  width: 120px;
  height: 100%;
  border-style: none;
  outline-style: none;
  background: rgba(14, 96, 247, 0.432);
  color: white;
  border-radius: 5px;
  transition: 0.2s ease-out;
  font-weight: 500;
  font-size: 15px;
  pointer-events: none;
  opacity: 0.7;
}

.login-btn:hover {
  transition: 0.2s ease-in;
  background: rgb(12, 75, 194);
}

.register-link {
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  margin-right: 15px;
  color: rgb(48, 48, 48);
}

.forgot-link {
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  color: rgb(48, 48, 48);
}

.register-link:hover,
.forgot-link:hover {
  color: rgb(14, 96, 247);
  text-decoration: underline;
}

.copyright {
  position: absolute;
  bottom: 30px;
  color: grey;
  font-size: 14px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}
