.order-wrapper {
  width: 100%;
  height: auto;
  background: rgb(240, 241, 241);
  padding-bottom: 20px;
}

.order {
  width: 75%;
  height: auto;
  min-height: 600px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  color: rgb(48, 48, 48);
  font-size: 14px;
  flex-direction: column;
}

.order-banner {
  width: 100%;
  height: 250px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.order-banner h1 {
  color: #e28a46;
  margin-top: 60px;
}

.order-table-h3 {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
}

.order-table-container {
  width: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
}

.order-table {
  width: 60%;
  min-height: 200px;
  font-size: 14px;
}

.order-received {
  width: 35%;
  min-height: 200px;
}

.order-rec {
  width: 100%;
  height: auto;
  min-height: 200px;
  background: white;
  font-size: 14px;
}

.table-header {
  width: 100%;
  height: 40px;
  background: #272727;
  display: flex;
  color: white;
}

.table-body {
  width: 100%;
  height: 40px;
  background: #ffffff;
  display: flex;
  border-bottom: 1px solid rgb(235, 235, 235);
  border-left: 1px solid rgb(235, 235, 235);
  border-right: 1px solid rgb(235, 235, 235);
  font-size: 13px;
}

.table-body:hover {
  cursor: pointer;
  background: rgb(243, 243, 243);
}

.header-no {
  width: 10%;
  min-width: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-date,
.header-status,
.header-action {
  width: 30%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 10px;
}

.header-action {
  justify-content: center;
}
