.homepage-loader {
  width: 100%;
  height: 100vh;
  position: fixed;
  background: rgba(255, 255, 255, 0.8);
  z-index: 6;
  display: flex;
  align-items: center;
  justify-content: center;
}

.homepage-wrapper {
  width: 100%;
  height: auto;
  position: relative;
  overflow: hidden;
}

.landing-page {
  display: flex;
  width: 100%;
  height: 100vh;
  background: white;
  justify-content: center;
  min-height: 700px;
  overflow: hidden;
}

.landing-page-container {
  width: 75%;
  height: 100%;
  min-width: 700px;
  display: flex;
}

.container-left {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  z-index: 2;

  min-width: 450px;
  padding-left: 20px;
  transform: translateX(-20px);
}

.container-left::before {
  width: 100%;
  height: 75%;
  position: absolute;
  top: 0;
  left: 00px;
  letter-spacing: 0;
  content: "";
  background: rgb(240, 240, 240);
  z-index: -1;
}

.container-right {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  position: relative;
}

.image-wrapper {
  width: 100%;
  height: auto;
}

.lp-image {
  width: 110%;
  object-fit: cover;
  position: absolute;
  right: -10%;
  top: 50%;
  transform: translateY(-50%);
  min-width: 500px;
  z-index: 3;
}

.image-1 {
  right: 20%;
  top: 15%;
}

.image-2 {
  right: 0;
  bottom: 15%;
}
.back-font {
  width: 2000px;
  height: auto;
  position: absolute;
  color: #272727;
  opacity: 0.05;
  top: 20%;
  left: 10%;
}

.back-font span {
  font-weight: bold;
  color: #272727;
  font-size: 200px;
  line-height: 200px;
  -webkit-user-select: none; /* Chrome all / Safari all */
  -moz-user-select: none; /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Likely future */
}

.sip-eat {
  letter-spacing: 4px;
  color: #272727;
  font-weight: 500;
  font-size: 18px;
  z-index: 2;
}

.container-left h1 span {
  color: #e28a46;
  z-index: 5;
}

.container-left h1 {
  color: #272727;
  font-size: 75px;
  line-height: 65px;
  font-weight: bolder;
  margin-bottom: 35px;
  z-index: 2;
}

.container p {
  color: #272727;
  font-size: 18px;
  z-index: 2;
}

.shop-now-btn {
  padding: 10px 20px;
  background: #e28a46;
  color: white;
  margin-top: 20px;
  cursor: pointer;
  z-index: 2;
  -webkit-user-select: none; /* Chrome all / Safari all */
  -moz-user-select: none; /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Likely future */
  text-decoration: none;
}

.shop-now-btn:hover {
  background: #c97a3e;
}

.social-media-btns {
  height: auto;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 10%;
  display: flex;
  padding-left: 20px;
}

.social-media-btns i {
  font-size: 25px;
  color: #808080;
  margin-right: 25px;
  cursor: pointer;
}

.shop-now-btn i {
  margin-left: 10px;
}

.landing-page-top,
.landing-page-btm {
  z-index: 3;
}

.landing-page-top {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  padding: 20px;
  border-radius: 5px;
}
.landing-page-top h1 {
  font-size: 4rem;
  font-weight: bold;
}
.landing-page-top p {
  font-size: 1.75rem;
  margin-top: 10px;
}

.landing-page-btm {
  display: flex;
}

.services {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 20px 0px;
  background: linear-gradient(180deg, #fff, #eee);
  overflow: hidden;
}

.services-wrapper {
  width: 75%;
  height: auto;
  display: flex;
  justify-content: center;
}

.our-services {
  width: 40%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 2rem;
  color: #272727;
  font-weight: bold;
  position: relative;
}

.our-services-back {
  position: absolute;
  left: 10%;
  font-size: 120px;
  line-height: 220px;
  color: #272727;
  opacity: 0.05;
}

.our-services-back h1 {
  z-index: -1;
  -webkit-user-select: none; /* Chrome all / Safari all */
  -moz-user-select: none; /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Likely future */
  text-decoration: none;
}

.our-services span {
  color: #e28a46;
}
.our-services i {
  color: #e28a46;
}

.what-we-served {
  width: 60%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #272727;
  z-index: 3;
}

.what-we-served p {
  color: #272727;
  z-index: 3;
}

.services-2 {
  width: 100%;
  min-height: 50px;
  margin: 0 auto;
  display: flex;
  background: #eee;
  justify-content: space-around;
  padding: 100px 0;
  position: relative;
  overflow: hidden;
  background-attachment: fixed;
}

.services-icon-wrapper {
  display: flex;
  width: 75%;
  justify-content: space-between;
}

.services-icon {
  width: 30%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
  padding: 10px 0px;
}

.services-icon-icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.services-icon-icon img {
  width: 120%;
  filter: drop-shadow(15px 8px 4px #00000010);
}

.services-des-title {
  display: flex;
  justify-content: center;
  padding: 20px;
  color: rgb(48, 48, 48);
  letter-spacing: 0.25px;
}

.services-des-body {
  display: flex;
  justify-content: center;
  text-align: center;
  color: rgb(48, 48, 48);
  font-size: 14px;
}

.menu {
  width: 75%;
  margin: 0 auto;
  height: auto;
  color: rgb(48, 48, 48);
  margin-bottom: 50px;
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
}

.menu-btm {
  width: 75%;
  display: flex;
  flex-direction: column;
  min-width: 600px;
  margin-left: 20px;
  min-height: 500px;
}

.menu-btm-div {
  margin-top: 20px;
  height: 100%;
  border: 1px solid lightgrey;
}

.menu-btm-top {
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  display: flex;
}

.menu-btm-btm {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  padding: 20px;
}

.see-all-link {
  font-weight: 500;
  cursor: pointer;
  transition: 0.1s ease-out;
  text-decoration: none;
  color: rgb(48, 48, 48);
  font-size: 18px;
}

.see-all-link:hover {
  transition: 0.1s ease-in;
  text-decoration: underline;
}

@media screen and (max-width: 768px) {
  .services-wrapper {
    width: 90%;
  }

  .services-2 {
    flex-direction: column;
    align-items: center;
  }

  .services-icon-wrapper {
    width: 90%;
  }

  .services-icon {
    margin-bottom: 20px;
    width: 50%;
  }
}

@media screen and (max-width: 648px) {
  .services-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .our-services {
    width: 100%;
    text-align: center;
    margin-bottom: 40px;
    font-size: 20px;
    padding: 0;
  }

  .services-icon-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .services-icon {
    margin-bottom: 20px;
    width: 90%;
  }

  .what-we-served {
    width: 100%;
  }

  .what-we-served h2 {
    font-size: 20px;
  }
}

@media screen and (max-width: 414px) {
  .services-icon {
    margin-bottom: 20px;
    width: 90%;
  }

  .services-icon img {
    width: 100%;
  }
}
