.product-form {
  width: 95%;
  height: 95vh;
  background: rgb(255, 255, 255);
  position: absolute;
  top: 50%;
  left: 50%;
  border: 1px solid lightgrey;
  border-radius: 10px;
  transform: translate(-50%, -50%);
  z-index: 2;
  box-shadow: 0px 8px 30px 0px rgba(49, 68, 80, 0.2);
}

.product-form-after {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.6);
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.product-form-after p {
  margin-bottom: 10px;
}

.product-form-header {
  width: 100%;
  height: 10%;
  min-height: 50px;
  border-bottom: 1px solid lightgrey;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 18px;
  position: relative;
}

.product-form-header i {
  position: absolute;
  font-size: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgb(34, 34, 34);
  transition: 0.2s ease-out;
}

.product-form-header i:hover {
  transition: 0.2s ease-in;
  background: rgb(226, 226, 226);
}

.product-form-body {
  width: 100%;
  height: 90%;
  padding: 0px 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form-input,
.form-select {
  width: 100%;
  height: 40px;
  border-style: none;
  outline: none;
  border: 1px solid lightgrey;
  border-radius: 10px;
  padding: 0px 20px;
  margin-bottom: 20px;
}

.product-form-body .textarea {
  width: 100%;
  height: 80px;
  border-style: none;
  outline: none;
  border: 1px solid lightgrey;
  border-radius: 10px;
  padding: 0px 20px;
  margin-bottom: 20px;
}

.input-div-file {
  width: 100%;
  height: auto;
  border: 1px solid lightgrey;
  border-radius: 10px;
  padding: 10px 20px;
  margin-bottom: 20px;
}

.input-div-file label {
  font-size: 13px;
  color: rgb(117, 117, 117);
}

.input-div-file-input {
  height: 30px;
  display: flex;
  align-items: center;
  position: relative;
  margin-top: 10px;
}

.custom-file-input::-webkit-file-upload-button {
  position: relative;
  padding-left: 25px;
  opacity: 0;
  height: 100%;
}

.custom-file-input::before {
  content: "Upload File";
  display: inline-block;
  background: rgb(228, 228, 228);
  border: 1px solid rgb(228, 228, 228);
  border-radius: 3px;
  padding: 5px 8px;
  outline: none;
  cursor: pointer;
  font-size: var(--font10);
  transition: 0.2s ease-out;
  color: var(--font);
  font-weight: border;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
}
.custom-file-input:hover::before {
  border-color: rgb(190, 190, 190);
  background: rgb(212, 212, 212);
  transition: 0.2s ease-in;
}
.custom-file-input:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}

.submit-btn-div {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}

.submit-btn-div input {
  width: 90px;
  height: 100%;
  margin-left: 20px;
  outline-style: none;
  border-radius: 5px;
  transition: 0.2s ease-out;
  cursor: pointer;
  font-weight: 500;
}

.form-add-btn {
  color: white;
  background: rgb(14, 96, 247);
  border: none;
}

.form-add-btn-diactive {
  color: white;
  background: rgba(14, 96, 247, 0.5);
  border: none;
  cursor: none;
  pointer-events: none;
}

.form-add-btn:hover {
  transition: 0.2s ease-in;
  background: rgb(12, 75, 194);
}

.form-cancel-btn {
  background: rgb(255, 255, 255);
  color: rgb(75, 75, 75);
  border: 1px solid lightgrey;
}

.form-cancel-btn:hover {
  background: rgb(240, 240, 240);
  transition: 0.2s ease-in;
}
