.product-wrapper {
  width: 90%;
  height: auto;
  min-height: 300px;
  max-height: 600px;
  margin: 0 auto;
  padding: 50px 0;
  display: flex;
  color: rgb(46, 46, 46);
  overflow-y: scroll;
}

.product-wrapper-left {
  width: 50%;
  height: 100%;
  justify-content: center;
  display: flex;
  align-items: flex-start;
}

.product-wrapper-left img {
  width: 50%;
  border: 1px solid #e28a46;
}

.product-wrapper-right {
  width: 50%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.product-wrapper-right div {
  position: absolute;
  right: 0px;
  top: 0px;
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 14px;
}

.product-p {
  padding: 5px 10px;
  transition: 0.2s ease-out;
  cursor: pointer;
  border-radius: 100px;
}

.product-p-2 {
  padding: 5px 10px;
  transition: 0.2s ease-out;
  cursor: pointer;
  border-radius: 100px;
}

.product-p i {
  margin-left: 5px;
}

.product-p-2 i {
  margin-left: 5px;
}

.product-p:hover {
  background: #f0f2f7;
  transition: 0.2s ease-in;
}

.product-p-2:hover {
  background: #f0f2f7;
  transition: 0.2s ease-in;
}

.admin-wrapper-after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.6);
  z-index: 3;
}
