.cart-page {
  width: 75%;
  height: auto;
  min-height: 600px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  color: rgb(48, 48, 48);
  font-size: 14px;
  flex-direction: column;
}

.cart-wrapper {
  width: auto;
  height: auto;
}

.cart-wrapper-after {
  width: 100%;
  height: 100vh;
  background: rgba(255, 255, 255, 0.8);
  position: fixed;
  z-index: 5;
}

.pop {
  width: 300px;
  height: 170px;
  background: rgb(255, 255, 255);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  padding: 10px 20px;
  border-radius: 5px;
  box-shadow: 0px 8px 30px 0px rgba(49, 68, 80, 0.2);
  border: 1px solid lightgrey;
}

.pop-top {
  width: 100%;
  height: 65%;
  color: rgb(48, 48, 48);
  display: flex;
  align-items: center;
  font-size: 15px;
}

.pop-after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.pop-bottom {
  height: 35%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.pop-bottom input {
  margin-left: 10px;
  width: 70px;
  height: 35px;
  cursor: pointer;
  border-style: none;
  border-radius: 3px;
  transition: 0.1s ease-out;
}

.confirm-input-pop {
  background: rgb(14, 96, 247);
  color: white;
}

.cancel-input-pop {
  color: rgb(14, 96, 247);
  background: white;
}

.cancel-input-pop:hover {
  background: rgb(238, 240, 241);
  transition: 0.1s ease-in;
}

.confirm-input-pop:hover {
  background: rgb(12, 75, 194);
  transition: 0.1s ease-in;
}

.image-cart-wrapper {
  width: 200px;
  height: 200px;
  margin-top: 20px;
}

.image-cart-wrapper img {
  width: 100%;
}

.cart-page-header {
  width: 100%;
  height: auto;
  padding: 20px 0px;
  padding-bottom: 10px;
}

.cart-page-directory {
  width: 100%;
  height: auto;
  font-size: 12px;
  margin-bottom: 30px;
  font-weight: 300;
}

.cart-page-body {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
  padding-bottom: 50px;
}

.cart-left {
  width: 65%;
  height: auto;
}

.cart-login {
  width: 100%;
  height: auto;
  border: 1px solid lightgrey;
  background: white;
  position: relative;
  padding-left: 75px;
  padding-top: 15px;
  border-radius: 10px;
  padding-bottom: 20px;
  margin-bottom: 15px;
}

.cart-login-float {
  position: absolute;
  left: 20px;
  width: 20px;
  height: 20px;
  border: 1px solid lightgrey;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  font-size: 14px;
}

.change {
  position: absolute;
  right: 20px;
  background: rgb(240, 240, 240);
  padding: 5px 10px;
  border-radius: 3px;
  color: rgb(48, 48, 48);
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 500;
  cursor: pointer;
  transition: 0.1s ease-out;
}

.change:hover {
  transition: 0.1s ease-in;
  background: rgb(228, 228, 228);
}

.cart-login-details {
  width: 100%;
  height: auto;
  color: rgb(48, 48, 48);
  display: flex;
  align-items: center;
  font-weight: 500;
}

.cart-right {
  max-width: 350px;
  width: 40%;
  height: 600px;
  background: rgb(255, 255, 255);
  border-radius: 10px;
  border: 1px solid rgb(233, 233, 233);
}

.cart-right-header {
  width: 90%;
  height: 10%;
  margin: 0 auto;
  border-bottom: 1px solid #eee;
  display: flex;
  align-items: center;
  color: rgb(48, 48, 48);
  justify-content: space-between;
}

.cart-right-header p {
  cursor: pointer;
}

.cart-right-header p:hover {
  text-decoration: underline;
}

.cart-right-body {
  width: 100%;
  height: 70%;
  overflow-y: scroll;
  background: rgb(248, 248, 248);
}

.cart-right-body-wrapper {
  width: 90%;
  height: 90px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  position: relative;
  border-bottom: 1px solid rgb(226, 226, 226);
}

.wrapper-image-wrapper {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 3px;
  margin-right: 20px;
}

.wrapper-image-wrapper img {
  width: 100%;

  object-fit: cover;
}

.wrapper-text-wrapper {
  width: auto;
  height: 60px;
}

.cart-right-body-wrapper i {
  position: absolute;
  right: 0px;
  color: rgb(80, 80, 80);
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
}

.cart-right-body-wrapper i:hover {
  background: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.cart-right-footer {
  width: 100%;
  height: 20%;
  background: rgb(255, 255, 255);
  border-top: 1px solid rgb(226, 226, 226);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.delivery-fee {
  width: 90%;
  height: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid lightgrey;
}

.total-fee {
  width: 90%;
  height: 40%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 18px;
}

.payment-method {
  width: 100%;
  background: rgb(248, 248, 248);
  height: 50px;
  border-radius: 10px;
  border: 1px solid rgb(241, 241, 241);
  display: flex;
  align-items: center;
  padding-left: 75px;
  position: relative;
  margin-bottom: 30px;

  margin-top: 30px;
}

.payment-method p {
  font-weight: 500;
}

.payment-method div {
  position: absolute;
  left: 20px;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  font-size: 14px;
  background: black;
  color: white;
}

.payment-option {
  width: 100%;
  height: auto;
}

.payment-debit {
  width: 100%;
  height: 65px;
  display: flex;
  padding-left: 20px;
  align-items: center;
  cursor: pointer;
  border-radius: 10px;
  margin-bottom: 10px;
}

.payment-debit:hover {
  box-shadow: 0px 8px 30px 0px rgba(93, 116, 131, 0.2);
}

.tick {
  width: 20px;
  height: 20px;
  border-radius: 100px;
  border: 1px solid lightgrey;
  margin-right: 35px;
}

.tick-active {
  width: 20px;
  height: 20px;
  border-radius: 100px;
  background: rgb(0, 0, 0);
  margin-right: 35px;
}

.payment-logo {
  width: 45px;
  height: 45px;
  border: 1px solid lightgrey;
  border-radius: 15px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.payment-logo-active {
  width: 45px;
  height: 45px;
  border: 1px solid rgb(48, 48, 48);
  border-radius: 15px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.payment-logo img {
  width: 90%;
}

.payment-logo-active img {
  width: 90%;
}

.debit-pay {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.pay-btn {
  width: 90px;
  height: 40px;
  cursor: pointer;
  border-style: none;
  outline-style: none;
  background: rgb(14, 96, 247);
  color: white;
  transition: 0.2s ease-out;
  font-weight: 500;
  font-size: 15px;
  margin-top: 20px;
}

.debit-pay input:hover {
  background: rgb(13, 83, 212);
}

.pay-btn-diactive {
  width: 90px;
  height: 40px;
  cursor: pointer;
  border-style: none;
  outline-style: none;
  background: rgb(14, 96, 247);
  color: white;
  transition: 0.2s ease-out;
  font-weight: 500;
  font-size: 15px;
  margin-top: 20px;
  opacity: 0.5;
  pointer-events: none;
}

.debit-form {
  width: 100%;
  height: auto;
  padding-left: 75px;
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  padding-bottom: 30px;
}

.debit-form-div {
  width: 100%;
  height: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}

.debit-form-div-div {
  display: flex;
  flex-direction: column;
  width: 48%;
}

.debit-form-div-div input {
  width: 100%;
}

.debit-form-div input {
  width: 100%;
  height: 40px;
  border: 1px solid lightgrey;
  outline: none;
  padding-left: 10px;
}

.debit-form label {
  margin-bottom: 5px;
  font-size: 12px;
}

.card-number {
  width: 100%;
  height: 40px;
  border: 1px solid lightgrey;
  outline: none;
  padding-left: 10px;
}

.debit-form input:focus {
  border: 1px solid rgb(48, 48, 48);
}
