.secondary-btn {
  outline: none;
  border-style: none;
  border: 1px solid #e49559;
  padding: 10px 20px;
  display: flex;
  height: auto;
  align-items: center;
  justify-content: center;
  color: #e6a26f;
  margin-right: 20px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s ease-in;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 500;
  background: transparent;
}

.secondary-btn:hover {
  transition: 0.2s ease-out;
  background: #e28a46;
  color: white;
}
