.dashboard {
  width: 80px;
  height: 100%;
  background: #e28a46;
}

.dashboard-logo {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #e28a46;
}

.dashboard-logo img {
  width: 35%;
}

.dashboard-links {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}

.dashboard-nav-links {
  width: 100%;
  height: 80px;
  text-decoration: none;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffc89e;
  flex-direction: column;
  font-size: 12px;
  font-weight: 300;
  margin-bottom: 10px;
}

.dashboard-nav-links:hover {
  color: #ffffff;
}

.dashboard-nav-links-active {
  width: 100%;
  height: 80px;
  text-decoration: none;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  flex-direction: column;
  font-size: 12px;
  font-weight: 500;
  margin-bottom: 10px;
  position: relative;
}

.dashboard-nav-links-active::after {
  content: "";
  position: absolute;
  width: 2px;
  height: 20%;
  background: #ffffff;
  border-radius: 100px;
  right: 0;
}

.dashboard-nav-links p,
.dashboard-nav-links-active p {
  margin-top: 5px;
}
