.address-form {
  width: 50%;
  min-width: 300px;
  height: 70%;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: rgb(255, 255, 255);
  z-index: 5;
  box-shadow: 0px 8px 30px 0px rgba(49, 68, 80, 0.2);
  border: 1px solid lightgrey;
  border-radius: 10px;
}

.address-form-header {
  width: 100%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.address-form-header i {
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  cursor: pointer;
  color: rgb(68, 68, 68);
}

.address-form-header i:hover {
  background: rgb(226, 226, 226);
}

.address-form-body {
  width: 100%;
  height: 80%;
  display: flex;
  padding: 0 20px;
  flex-direction: column;
  padding-top: 20px;
}

.address-form-body label {
  color: rgb(48, 48, 48);
  width: 100%;
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: 500;
}

.address-form-body select,
.address-form-body input {
  width: 100%;
  height: 40px;
  outline: none;
  border: 1px solid lightgrey;

  padding: 0 10px;
  margin-bottom: 20px;
}

.submit-wrapper {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}

.submit-wrapper input {
  width: 90px;
  height: 45px;
  padding: 0;
  border-radius: 5px;
  margin-left: 20px;
  cursor: pointer;
  border: none;
  font-weight: 300;
}

.address-submit-btn {
  background: rgb(14, 96, 247);
  color: white;
  transition: 0.2s ease-out;
}

.address-cancel-btn {
  color: rgb(14, 96, 247);
  background: white;
  transition: 0.2s ease-out;
}

.address-cancel-btn:hover {
  background: rgb(238, 240, 241);
}

.address-submit-btn:hover {
  background: rgb(12, 75, 194);
}
