.primary-btn {
  background: #e28a46;
  outline: none;
  border-style: none;
  padding: 10px 20px;
  display: flex;
  height: auto;
  align-items: center;
  justify-content: center;
  color: white;
  margin-right: 20px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s ease-in;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 500;
}

.primary-btn:hover {
  transition: 0.2s ease-out;
  background: #b9713a;
}
