.delete {
  width: 400px;
  height: 200px;
  background: rgb(255, 255, 255);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  border-radius: 10px;
  border: 1px solid rgb(226, 226, 226);
  box-shadow: 0px 8px 30px 0px rgba(93, 116, 131, 0.3);
}

.delete-after {
  width: 100%;
  height: 100%;
  position: absolute;
  background: rgba(255, 255, 255, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
}

.delete-top {
  width: 100%;
  height: 65%;
  position: relative;
  padding: 20px;
  padding-top: 25px;
  color: rgb(48, 48, 48);
}

.delete-top::after {
  content: "";
  width: 90%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  border-bottom: 1px solid rgb(226, 226, 226);
}

.delete-btm {
  width: 100%;
  height: 35%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0px 20px;
}

.delete-btm input {
  width: 90px;
  height: 40px;
  margin-left: 15px;
  border-radius: 8px;
  outline: none;

  cursor: pointer;
  transition: 0.2s ease-out;
}

.cancel-delete {
  background: rgb(255, 255, 255);
  color: rgb(75, 75, 75);
  border: 1px solid lightgrey;
}

.delete-btn {
  background: rgb(255, 24, 24);
  color: rgb(255, 255, 255);
  border-style: none;
}

.delete-btn:hover {
  transition: 0.2s ease-out;
  background: rgb(206, 20, 20);
}

.cancel-delete:hover {
  background: rgb(240, 240, 240);
  transition: 0.2s ease-in;
}
