.error-msg {
  width: 100%;
  height: auto;
  top: 50px;
  left: 0;
  position: absolute;
  padding: 0px 50px;
}

.error-msg p {
  background: rgb(255, 103, 131);
  height: 100%;
  padding: 15px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 5px;
  color: rgb(255, 255, 255);
  font-size: 14px;
}
