.admin-header {
  width: 100%;
  min-height: 80px;
  height: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

.admin-header-wrapper {
  width: 90%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.admin-profile {
  display: flex;
  align-items: center;
}

.admin-profile i {
  color: rgb(46, 46, 46);
}

.profile-drop:hover,
.notification-drop:hover {
  transition: 0.2s ease-in;
  background: #e2e3e7;
}

.profile-drop,
.notification-drop {
  position: relative;
  background: #f0f2f7;
  width: 45px;
  height: 45px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  cursor: pointer;
  transition: 0.2s ease-out;
  position: relative;
  border: 4px solid #f0f2f700;
  color: rgb(48, 48, 48);
}

.profile-drop-active,
.notification-drop-active {
  position: relative;
  background: #e2e3e7;
  width: 45px;
  height: 45px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  cursor: pointer;
  transition: 0.2s ease-out;
  position: relative;
  border: 4px solid #f0f2f7;
}

.profile-drop-active i,
.notification-drop-active i {
  color: rgb(55, 123, 248);
}

.profile-drop-after {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 120%;
  right: 0;
  z-index: 3;
  border-radius: 5px;
  background: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px SOLID LIGHTGREY;
}

.notification-drop-after {
  width: 300px;
  height: 500px;
  background: rgb(255, 255, 255);
  position: absolute;
  top: 120%;
  right: 0;
  z-index: 3;
  border-radius: 5px;
  box-shadow: 0px 8px 30px 0px rgba(49, 68, 80, 0.1);
  border: 1px solid rgb(228, 228, 228);
}

.see-all-notification {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: rgb(3, 3, 29);
}

.see-all-notification p:hover {
  text-decoration: underline;
}

.notification-body {
  width: 100%;
  height: 450px;
  overflow-y: scroll;
  padding: 15px;
  color: rgb(46, 46, 46);
}
