.footer {
  width: 100%;
  height: auto;
  min-height: 200px;
  background: #1b1f24;
  padding-top: 50px;
  display: flex;
  justify-content: center;
}

.footer-wrapper {
  width: 80%;
  height: auto;
}

.footer-top {
  width: 100%;
  height: auto;
  display: flex;
  padding-bottom: 20px;
  justify-content: space-between;
  border-bottom: 1px solid rgb(167, 167, 167);
}

.footer-top-left {
  width: 30%;
  padding-right: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.footer-top-left-bottom {
  display: flex;
}

.footer-top-left-bottom i {
  color: rgb(167, 167, 167);
  margin-right: 20px;
  font-size: 20px;
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

.fb:hover {
  color: #1877f2;
  transition: 0.1s ease-in-out;
}

.ig:hover {
  transition: 0.1s ease-in-out;

  color: #cf1d42;
}

.in:hover {
  color: #0270ad;
  transition: 0.1s ease-in-out;
}

.tw:hover {
  color: #5da9dd;
  transition: 0.2s ease-in-out;
}

.footer-top-left h2 {
  margin-bottom: 10px;
  color: #e28a46;
}

.footer-top-left p {
  color: rgb(167, 167, 167);
}

.footer-top-right {
  width: 60%;
  display: flex;
}

.footer-links {
  width: 33.33%;
}

.header-links-p {
  color: white;
  font-weight: 500;
  margin-bottom: 10px;
  font-size: 14px;
}

.footer-links ul {
  display: flex;
  flex-direction: column;
}

.footerLinks {
  color: rgb(167, 167, 167);
  text-decoration: none;
  font-size: 14px;
  margin-bottom: 5px;
  transition: 0.1s ease-out;
}

.footerLinks:hover {
  color: white;
  transition: 0.1s ease-in;
  text-decoration: underline;
}

.contact-p {
  color: rgb(167, 167, 167);
  font-size: 14px;
}

.footer-btm {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-btm-left {
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
  color: rgb(167, 167, 167);
  font-size: 13px;
}

.footer-btm-right {
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
  color: rgb(167, 167, 167);
  font-size: 13px;
}

.footer-btm-right p {
  margin-left: 20px;
  cursor: pointer;
  transition: 0.1s ease-out;
}

.footer-btm-right p:hover {
  color: white;
  transition: 0.1s ease-in;
}
