.menu-wrapper {
  width: auto;
  height: auto;
  position: relative;
}

.menu-wrapper-after {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  overflow-y: hidden;
}

.menu-loader {
  width: auto;
  height: 50px;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 6;
  font-weight: 500;
  color: rgb(48, 48, 48);
}

.menu-loader p {
  margin-bottom: 10px;
}

.menu-toast {
  position: fixed;
  bottom: 20px;
  right: 20px;
  min-width: 200px;
  background: #bd6f33;
  color: rgb(255, 255, 255);
  border-radius: 2px;
  font-size: 12px;
  z-index: 4;
  padding: 12px 20px;
  font-weight: 300;
  display: flex;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px,
    rgba(0, 0, 0, 0.05) 0px 5px 10px;
}

.menu-toast i {
  font-size: 12px;
  cursor: pointer;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  margin-left: 15px;
}

.menu-toast i:hover {
  background: rgba(226, 226, 226, 0.507);
}

.menu-toast:hover {
  opacity: 1;
  user-select: none;
}

.menu-after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  overflow: hidden;
}

.menu-banner {
  width: 100%;
  height: 300px;
  background: url("./menu.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-banner p {
  font-size: 18px;
  color: rgb(223, 223, 223);
  z-index: 2;
}

.menu-banner::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.4);
}

.banner-wrapper {
  width: 75%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.banner-wrapper h1 {
  color: white;
  z-index: 2;
}

.menu-nav {
  width: 25%;
  min-width: 200px;
  min-height: 300px;
  height: 50%;
  color: #272727;
}

.menu-nav div {
  margin-top: 20px;
  background: #f4f4f4;
  padding: 20px;
}

.menu-nav div ul {
  margin-top: 6px;
  padding-left: 10px;
  margin-bottom: 20px;
}

.menu-nav-list {
  text-decoration: none;
  list-style: none;
  font-size: 14px;
  color: #272727;
  font-weight: 300;
  margin-bottom: 5px;
}

.menu-nav-list:hover {
  text-decoration: underline;
  color: rgb(14, 96, 247);
}

.menu-nav-list li {
  margin-bottom: 5px;
}

.search-menu {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: relative;
}

.search-menu input {
  width: 30%;
  min-width: 300px;
  height: 100%;
  border: 1px solid lightgrey;
  border-radius: 3px;
  padding-left: 10px;
  outline: none;
  padding-right: 10px;
}

.search-menu input:focus {
  border: 1px solid #e28a46;
}
