.admin-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
}

.admin-sidebar {
  width: 5%;
  min-width: 90px;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.admin-sidebar i {
  display: flex;
  align-items: center;
  justify-content: center;
  color: grey;
  margin: 25px 0px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  transition: 0.1s ease-out;
  color: #e28a46;
}

.admin-sidebar i.active {
  color: #e28a46;
}

.admin-sidebar i:hover {
  background: #e3e3e9;
  transition: 0.1s ease-in;
  color: #e28a46;
}

.admin-content {
  width: 95%;
  height: 100%;
  background: #fff;
}

.admin-content-wrapper {
  background: white;
  width: 98%;
  height: 98%;
  border-radius: 30px;
}

.admin-logo {
  width: 100%;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.admin-logo img {
  width: 45%;
  background: #e28a46;
  padding: 5px;
  border-radius: 5px;
}

.admin-header input {
  width: 30%;
  min-width: 300px;
  height: 40px;
  border-radius: 100px;
  border-style: none;
  outline: none;
  border: 1px solid rgb(235, 235, 235);
  background: #f6f5fa;
  padding: 0px 15px;
}

.admin-header input:focus {
  border: 1px solid lightgrey;
}

.admin-header h3 {
  color: rgb(46, 46, 46);
}

.admin-body {
  width: 90%;
  margin: 0 auto;
  height: 88%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.list-of-product {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  position: relative;
}

.product-header {
  width: 100%;
  height: 10%;
  display: flex;
  align-items: center;
  font-weight: 500;
  color: rgb(46, 46, 46);
}

.product-body {
  height: 90%;
  width: 100%;
}

.add-btn {
  position: absolute;
  right: 50px;
  bottom: 50px;
  background: rgb(14, 96, 247);
  color: white;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  cursor: pointer;
  transition: 0.2s ease-out;
}

.add-btn:hover {
  transition: 0.2s ease-in;
  background: rgb(12, 75, 194);
}

.list-header {
  width: 100%;
  height: 50px;
  border: 1px solid rgb(235, 235, 235);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.product-name {
  width: 35%;
  color: rgb(53, 53, 53);
  display: flex;
  align-items: center;
}

.product-no {
  width: 5%;
  color: rgb(53, 53, 53);
}

.product-quantity,
.product-price,
.product-category {
  width: 20%;
  display: flex;
  justify-content: center;
  color: rgb(53, 53, 53);
}

.list-body {
  width: 100%;
  height: 50px;

  /* border-bottom: 1px solid rgb(235, 235, 235); */
  display: flex;
  align-items: center;
  padding-left: 20px;
  font-size: 14px;
  text-decoration: none;
}

.list-body-wrapper {
  width: 100%;
  height: 90%;
  overflow-y: scroll;
  background: rgb(250, 250, 250);
  border-bottom: 1px solid rgb(235, 235, 235);
  border-right: 1px solid rgb(235, 235, 235);
  border-left: 1px solid rgb(235, 235, 235);
}

.product-image-wrapper {
  width: 35px;
  height: 35px;
  margin-right: 20px;
  overflow: hidden;
  border-radius: 100px;
  border: 1px solid lightgrey;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product-image-wrapper img {
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.list-body:hover {
  background: white;
  cursor: pointer;
  box-shadow: 0px 8px 30px 0px rgba(128, 149, 161, 0.1);
}
